const DBG2 = false;

const SUGGESTED_PHRASE_LENGTH = 3;
const PBKDF2_10M_ITERATIONS = 10 * 1000 * 1000;

/** @public */
export type StrongphraseParams = { extraEntropy?: string; words?: number };

/** @public */
export const strongphraseVersion = "3.20240317.0";

/**
 * generatePassPhrase()
 * 
 * Generates a passphrase from a list of 16K words. Results in 14 bits of reasonably
 * strong entropy per word. 3 (the default) should be fine for most purposes.
 * 
 * Parameters can be passed as a string (optional extra entropy), number (number of pass phrase words),
 * or if you want to specify both, as an object with the following properties:
 * 
 * @example
 * ```typescript
 *   {
 *     extraEntropy: "some extra entropy",
 *     words: 3
 *   }
 * ```
 * 
 * Number of words defaults to '3', and extraEntropy defaults to none (duh),
 * so you can also just call it without any parameters. It will return the phrase with
 * the words separated by spaces.
 * 
 * Note that the word list only contains lower case letters and words. Also please 
 * note there may still be a few undesirable words in the list, please notify us
 * if you find any. 
 * 
 * Generally, for a user interface, you should generally not allow the user
 * to hit "regenerate", instead we would suggest that you generate a set of phrases,
 * and they can pick one. That way you are more likely to constrain the loss of
 * entropy (which would be ln2() of number of choices). A good number is probably 4,
 * which with a choice of 3 words will total 40 bits of entropy, a general good amount.
 * 
 * The 'some extra entropy' can either be another external source (eg random.org),
 * or some biometric data (eg mouse movements, keystrokes, etc), or simply offering
 * the user to type in a bunch of "random" characters on their keyboard. Regardless,
 * this code does not assume any particular quality of that additional entropy, it
 * can be terrible and it won't matter (much).
 * 
 * For VERY strong phrases, we would suggest five (5) words, and allow the user to
 * chose among 8 phrases. 
 * 
 * @public
 * 
 */
export async function generatePassPhrase(params?: string | number | StrongphraseParams): Promise<string> {
    let extraEntropy: string | undefined;
    let words: number | undefined;

    const RND_BUFFER_SIZE = 32; // 256 bits
    const MAX_WORDS = 16; // 256 bits divided by 16
    const WORD_LIST_SIZE_LN2 = 14; // 2^14 = 16384
    const WORD_LIST_SIZE = 2 ** WORD_LIST_SIZE_LN2;

    if (typeof params === 'string') {
        extraEntropy = params;
    } else if (typeof params === 'number') {
        words = params;
    } else if (params) {
        extraEntropy = params.extraEntropy;
        words = params.words;
    }

    if (!words || words < 1) words = SUGGESTED_PHRASE_LENGTH;
    if (words > MAX_WORDS) throw new Error(`Too many words requested, max is ${MAX_WORDS}.`);

    // sanity check make sure our word array is the expected size
    if (words16K_v03.length !== WORD_LIST_SIZE) throw new Error("Word list is not the expected size (16x1024).")

    // Generate 256 random bits from crypto
    const randomValues = globalThis.crypto.getRandomValues(new Uint8Array(RND_BUFFER_SIZE));

    let entropySource = randomValues;

    // If extraEntropy is provided, hash it and append to the randomValues, and then hash the result
    if (extraEntropy) {
        const encoder = new TextEncoder();
        const extraEntropyHash = await globalThis.crypto.subtle.digest('SHA-256', encoder.encode(extraEntropy));

        const combinedEntropySource = new Uint8Array([...randomValues, ...new Uint8Array(extraEntropyHash)]);
        entropySource = new Uint8Array(await globalThis.crypto.subtle.digest('SHA-256', combinedEntropySource));
    }

    // Create a Uint16 view of the entropy source
    const indices = new Uint16Array(entropySource.buffer);

    // Map the indices to words and join them into a passphrase
    const passphrase = Array.from(indices, i => words16K_v03[i & (WORD_LIST_SIZE - 1)]).slice(0, words).join(' ');

    return passphrase;
};

