/**
 * generateStrongKey()
 * 
 * Uses PBKDF2 (10M iterations, SHA-256) to generate a strong key from a passphrase.
 * Returns an object with the following properties:
 * 
 * @example
 * ```typescript
 * {
 *  phrase: "passphrase",  // whatever passphrase was used
 *  key: CryptoKey,
 *  salt: Uint8Array(16),
 *  iterations: 10000000
 * }
 * ```
 * 
 * @see https://developer.mozilla.org/en-US/docs/Web/API/SubtleCrypto/deriveKey
 * 
 * Roughly speaking, 10M is considered very strong. 1M is considered strong. 100K is considered
 * reasonable. 10K is considered weak. 1K is considered very weak. Currently this cannot
 * be overriden by the caller, since modern clients should handle this fine. If you need
 * a quicker generator, we suggest you create a new function that is clearly named as
 * such (eg 'generateQuickAndDirtyWeakKey()').
 * 
 * @public
 * 
 */
export async function generateStrongKey(passphrase?: string) {
    // Generate a random salt
    const salt = globalThis.crypto.getRandomValues(new Uint8Array(16));
    if (!passphrase) passphrase = await generatePassPhrase(SUGGESTED_PHRASE_LENGTH);
    return await _generateKey(passphrase, salt, PBKDF2_10M_ITERATIONS);
}

/**
 * recreateStrongKey()
 * 
 * Takes precise parameters (typically from a previous call to generateStrongKey())
 * and recreates the strong key.  You should not use this to create a new key
 * unless you are sure you know what you are doing ... use generateStrongKey() instead.
 * @public
 */
export async function recreateStrongKey(passphrase: string, salt: Uint8Array, iterations: number) {
    // todo: some sanity checking of parameters
    return await _generateKey(passphrase, salt, iterations);
}

async function _generateKey(passphrase: string, salt: Uint8Array, iterations: number) {
    const encoder = new TextEncoder();
    const passphraseKey = encoder.encode(passphrase);

    // Import the passphrase into a CryptoKey
    const baseKey = await globalThis.crypto.subtle.importKey(
        'raw',
        passphraseKey,
        { name: 'PBKDF2' },
        false,
        ['deriveBits', 'deriveKey']
    );

    // Derive a strong key
    const strongKey = await globalThis.crypto.subtle.deriveKey(
        {
            name: 'PBKDF2',
            salt: salt,
            iterations: iterations,
            hash: 'SHA-256'
        },
        baseKey,
        // You can define the specifics of your derived key
        { name: 'AES-GCM', length: 256 },
        true,
        ['encrypt', 'decrypt']
    );

    const returnKey = {
        phrase: passphrase,
        key: strongKey,
        salt: salt,
        iterations: iterations,
    }

    if (DBG2) {
        console.log("Strong key generated: ");
        console.log(returnKey);
    }
    return returnKey;
}


